@media screen and (max-width: 451px) {
  /* Navbar */
  nav ul {
    width: 70%;
    height: calc(100vh - 60px);
    position: fixed;
    bottom: 0;
    right: -100%;
    flex-direction: column;
    background: #000;
    justify-content: space-evenly;
    align-items: center;
    color: gold;
    transition: 0.6s;
    z-index: 3;
  }
  nav ul.active {
    right: 0;
    bottom: 0;
  }
  nav ul li:hover .line {
    width: 100%;
  }
  nav ul li:focus .line {
    width: 100%;
  }
  nav ul h3 {
    display: block;
  }
  .hamburger {
    width: 40px;
    height: 42px;
    margin-right: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .hamburger div {
    width: 100%;
    height: 3px;
    background: #fff;
  }
  /* Footer */
  .messangers {
    width: 60%;
  }
  .date {
    width: 70%;
  }
  .headermenu {
    height: calc(100vh - 90px);
    background: url("https://i.pinimg.com/736x/36/37/42/36374223a47ff3c99d113894322fc43b.jpg");
    background-size: contain;
    background-color: rgb(10, 10, 200);
    background-blend-mode: color-burn;
  }
  .headermenu h3 {
    font-size: 40px;
    margin-left: 40px;
    margin-top: 120px;
  }
  .headermenu h4 {
    margin-left: 40px;
    margin-top: 20px;
  }
  .headermenu button {
    margin-left: 40px;
    margin-top: 20px;
  }
}
