* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Navbar */
nav {
  height: 60px;
  background: rgb(20, 19, 19);
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0px 3px 5px #ccc;
}
nav h3 {
  padding: 10px;
  margin-left: 10px;
}
nav ul {
  width: 300px;
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin: 0;
}
nav ul li {
  padding: 5px;
  cursor: pointer;
}
nav ul li div {
  width: 0%;
  height: 3px;
  background: red;
  transition: 0.6s;
}
nav ul li:hover .line {
  width: 100%;
}
nav ul h3 {
  display: none;
}
.hamburger {
  display: none;
}
/* Footer */
footer {
  height: 250px;
  background: rgb(20, 19, 19);
  color: #fff;
  padding-top: 15px;
}
footer h4 {
  font-weight: 500;
  text-align: center;
}
.messangers {
  width: 20%;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px auto;
}
.messangers > .messanger {
  width: 60px;
  height: 100%;
  border-radius: 50%;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  color: #000;
  cursor: pointer;
}
.messanger:nth-child(2) {
  font-size: 40px;
}
.date {
  width: 15%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 10px;
}
.wrapper {
  width: 90%;
  background: #000;
  margin: 0 auto;
}
.headermenu {
  width: 100%;
  height: calc(100vh - 60px);
  margin-top: 60px;
  background: url("https://dpbnri2zg3lc2.cloudfront.net/en/wp-content/uploads/2021/01/web_dev_pillar_page.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-color: rgb(10, 10, 200);
  background-blend-mode: color-burn;
  color: #fff;
}
.headermenu h3 {
  font-size: 60px;
  margin-left: 200px;
  display: inline-block;
  margin-top: 200px;
}
.headermenu h4 {
  margin-left: 200px;
  margin-top: 15px;
}
.headermenu button {
  padding: 10px;
  margin-left: 200px;
  margin-top: 10px;
}
.link {
  text-decoration: none;
  color: #fff;
}
.link:hover {
  color: #fff;
  text-decoration: none;
}
.link_f {
  color: #000;
  margin-top: -10px;
}
.link_f:hover {
  color: #000;
}
